import * as React from 'react';
import { Button, Typography, Tabs, TabsHeader, Tab, TabsBody, TabPanel, Card, Collapse, Radio } from '@material-tailwind/react';
import { clsx } from 'clsx';
import api from '../../../../../../utils/axios';
import Select from 'react-select';
import { decode } from 'he';

export default function VerificationDocument() {
  const [dataVerDocList, setDataVerDocList] = React.useState<any>([]);
  const [dataFormVerDoc, setDataFormVerDoc] = React.useState<any>([]);
  const [checkedValues, setCheckedValues] = React.useState({});

  const [isOpenCollapse, setIsOpenCollapse] = React.useState<boolean>(false);
  const [activeIdCollapse, setActiveIdCollapse] = React.useState<string>('');

  const [detailValueDocument, setDetailValueDocument] = React.useState<any>([]);
  const [fieldsValueDocument, setFieldsValueDocument] = React.useState<any>(null);
  const [detailApplicant, setDetailApplicant] = React.useState<any>(null);

  const [activeApplicantId, setActiveApplicantId] = React.useState<string>('');
  const [listDashboardMini, setListDashboardMini] = React.useState([]);

  const getDashboardMini = async () => {
    try {
      const response = await api.post('screening/document/dashboard/mini', {});
      const result = response.data.data;
      setListDashboardMini(result);
      console.log(' ~ getDashboardMini ~ result:', result);
    } catch (error) {
      console.error(error);
    }
  };

  const getDataVerDocList = async () => {
    try {
      const payload = {
        search: '',
        page: 1,
        size: 10,
        sort: '',
        category: 'draft',
        date_distribute: '',
      };
      const response = await api.post('/screening/document/list', payload);
      const result = response.data.data;
      setDataVerDocList(result);
      console.log(' ~ getDataVerDocList ~ result:', result);
    } catch (error) {
      console.log(' ~ getDataVerDocList ~ error:', error);
    }
  };

  const getScreeningForm = async (applicant_id: string) => {
    const form_document = localStorage.getItem('form_document');

    if (form_document) {
      const parse_form_document = JSON.parse(form_document);
      console.log(' ~ getScreeningForm ~ parse_form_document:', parse_form_document);
      setDataFormVerDoc(parse_form_document);
    }

    try {
      const payload = { applicant_id };
      const response = await api.post('/screening/document/form', payload);
      const result = response.data.data;
      localStorage.setItem('form_document', JSON.stringify(result));
      setDataFormVerDoc(result);
    } catch (error) {
      console.log(' ~ getScreeningForm ~ error:', error);
    }
  };

  const getScreeningDetailDocument = async (applicant_id: string) => {
    setActiveApplicantId(applicant_id);
    // const detail_document = localStorage.getItem('detail_document');

    // if (detail_document) {
    //   const parse_detail_document = JSON.parse(detail_document);
    //   console.log(desctructuredObject(parse_detail_document));
    //   setDetailValueDocument(parse_detail_document);
    //   setFieldsValueDocument(parse_detail_document);
    //   return;
    // }
    try {
      // setFieldsValueDocument(detailJSON);
      // setDetailValueDocument(detailJSON);
      const payload = { applicant_id };
      const response = await api.post('/screening/document/detail', payload);
      const result = response.data.data.shift();
      // console.log(' ~ getScreeningDetailDocument ~ result:', result);
      // localStorage.setItem('detail_document', JSON.stringify(result));
      setDetailValueDocument(result);
      setFieldsValueDocument(result);
    } catch (error) {
      console.log(' ~ getScreeningForm ~ error:', error);
    }
  };

  const resetState = () => {
    setDataFormVerDoc([]);
    setDetailValueDocument([]);
    setFieldsValueDocument(null);
    setDetailApplicant(null);
  };

  const handleChangeValueScreeningDocument = (e) => {
    const { name, value } = e.target;
    setFieldsValueDocument((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const getDataDetailApplicant = async (applicant_id: string) => {
    try {
      const response = await api.post('/screening/document/applicant', {
        applicant_id,
      });
      const result = response.data.data;
      console.log(' ~ getDataDetailApplicant ~ result:', result);
      setDetailApplicant(result);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    getDataVerDocList();
    getDashboardMini();
  }, []);

  const handleSubmit = () => {
    /**
     * Remove ocr_ for payload submit screener
     */
    const newPayload = Object.keys(fieldsValueDocument).reduce((acc, key) => {
      if (!/^ocr_/.test(key)) {
        acc[key] = fieldsValueDocument[key];
      }
      return acc;
    }, {});

    console.log(newPayload);
  };

  return (
    <main className="-my-5 h-[89vh] flex flex-col">
      {/* <Header />
        <MainContainer
          dataVerDocList={dataVerDocList}
          getScreeningForm={getScreeningForm}
          getScreeningDetailDocument={getScreeningDetailDocument}
          fieldsValueDocument={fieldsValueDocument}
          handleChangeValueScreeningDocument={handleChangeValueScreeningDocument}
          handleSubmit={handleSubmit}
        /> */}
      <Header listDashboardMini={listDashboardMini} />
      <div className="flex-1 flex flex-row overflow-y-hidden mt-2 space-x-2">
        <div className="w-1/4 overflow-y-scroll bg-white p-2 rounded-t-lg border">
          <ScreeningTabs
            dataTable={dataVerDocList}
            getScreeningForm={getScreeningForm}
            getScreeningDetailDocument={getScreeningDetailDocument}
            getDataDetailApplicant={getDataDetailApplicant}
            resetState={resetState}
            activeApplicantId={activeApplicantId}
          />
        </div>

        <div className="flex flex-col flex-1">
          <div className="flex overflow-y-hidden space-x-2 h-full">
            {dataFormVerDoc.length > 0 && (
              <main className="w-1/3 overflow-y-scroll bg-white rounded-t-lg p-2 space-y-2">
                {dataFormVerDoc.map((validationData, index) => {
                  return (
                    <ValidationCard key={validationData.id} validationData={validationData} fieldsValueDocument={fieldsValueDocument}>
                      {validationData.list_fields.map((validationValue, index) => {
                        return (
                          <ValidationComponentValue
                            key={validationValue.id}
                            fieldsValueDocument={fieldsValueDocument}
                            handleChangeValueScreeningDocument={handleChangeValueScreeningDocument}
                            validationData={validationData}
                            validationValue={validationValue}
                          />
                        );
                      })}
                    </ValidationCard>
                  );
                })}
                <ValidationCard validationData={{ doc_name: 'akhir' }} fieldsValueDocument={fieldsValueDocument}>
                  <LastValidationComponentValue
                    fieldsValueDocument={fieldsValueDocument}
                    handleChangeValueScreeningDocument={handleChangeValueScreeningDocument}
                  />
                </ValidationCard>
              </main>
            )}
            {detailApplicant && (
              <main className="w-2/3 bg-white rounded-t-lg overflow-y-scroll p-4 space-y-2">
                <h1 className="text-center text-xl font-semibold">{detailApplicant.vacancy.title}</h1>
                <div className="wysiwyg max-w-prose" dangerouslySetInnerHTML={{ __html: decode(detailApplicant.vacancy.description) }} />
                <div className="p-3 rounded-lg bg-gray-50 border text-slate-600 drop-shadow-sm space-y-1">
                  <h5 className="text-sm font-semibold text-slate-800">Major Qualification</h5>
                  <p className="text-xs">{detailApplicant.vacancy.stream_name}</p>
                </div>
                <div className="space-y-3 pt-2">
                  <h3 className="font-bold text-blue-700 text-lg">Pendidikan</h3>
                  <hr className="border-[0.5px] border-gray-300" />
                  <div>
                    <table className="w-full text-left border-separate border-spacing-0 rounded-lg border overflow-hidden">
                      <thead>
                        <tr>
                          <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">NIM</th>
                          <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Universitas</th>
                          <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Jurusan</th>
                          <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Jenjang Pendidikan</th>
                          <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">IPK</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['student_no']}</td>
                          <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['education_name']}</td>
                          <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['major_name']}</td>
                          <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['education_level']}</td>
                          <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['gpa']}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
                <div className="space-y-3 pt-2">
                  <h3 className="font-bold text-blue-700 text-lg">Dokumen Pendukung</h3>
                  <hr className="border-[0.5px] border-gray-300" />
                  {Object.entries(detailApplicant.other_docs).map(([key, value]: any) => {
                    return (
                      <div key={key}>
                        <PreviewFile user_applicant_id={detailApplicant.personal_info.user_id} filename={value} />
                      </div>
                    );
                  })}
                </div>
              </main>
            )}
          </div>
          <footer className="p-2 flex justify-center items-center bg-white border-t space-x-2">
            <button onClick={() => alert('save draft')} className="px-4 py-2 rounded-lg bg-white border border-blue-700 text-blue-700 text-xs">
              Save Draft
            </button>
            <button onClick={handleSubmit} className="px-4 py-2 rounded-lg bg-blue-700 border text-white text-xs">
              Submit
            </button>
          </footer>
        </div>
      </div>
    </main>
  );
}

function Header({ listDashboardMini }) {
  return (
    <header className="space-y-2">
      <div className="px-4 py-2 rounded-lg border border-blue-700 text-blue-700 inline-block bg-blue-50">
        <b>Total</b> : {listDashboardMini.total}
      </div>
      <hr />
      <div className="space-x-4">
        <div className="px-4 py-1.5 rounded-full inline-block bg-white font-light text-xs">
          Screening: <span className="font-bold">{listDashboardMini.total_draft}</span>
        </div>
        <div className="px-4 py-1.5 rounded-full inline-block bg-white font-light text-xs">
          QC: <span className="font-bold">{listDashboardMini.total_qc}</span>
        </div>
        <div className="px-4 py-1.5 rounded-full inline-block bg-white font-light text-xs">
          Done: <span className="font-bold">{listDashboardMini.total_done}</span>
        </div>
        <div className="px-4 py-1.5 rounded-full inline-block bg-green-50 text-green-500 font-light text-xs">
          Lulus: <span className="font-bold">{listDashboardMini.total_lulus}</span>
        </div>
        <div className="px-4 py-1.5 rounded-full inline-block bg-red-50 text-red-500 font-light text-xs">
          Gugur: <span className="font-bold">{listDashboardMini.total_gugur}</span>
        </div>
      </div>
      <hr />
      <div className="grid grid-cols-8 gap-x-4">
        <div className="col-span-6">
          <input
            type="text"
            className="w-full px-5 py-2 border border-gray-100 rounded focus:outline-none focus:ring-2 focus:ring-blue-700 focus:ring-offset-2"
          />
        </div>
        <div className="col-span-2">
          <input
            type="text"
            className="w-full px-5 py-2 border border-gray-100 rounded focus:outline-none focus:ring-2 focus:ring-blue-700 focus:ring-offset-2"
          />
        </div>
      </div>
      <hr />
    </header>
  );
}

