type TScreeninTab = {
  dataTable: [];
  getScreeningForm: (applicant_id: string) => void;
  getScreeningDetailDocument: (applicant_id: string) => void;
  getDataDetailApplicant: (applicant_id: string) => void;
  resetState: () => void;
  activeApplicantId: string;
};

function ScreeningTabs({
  dataTable,
  getScreeningForm,
  getScreeningDetailDocument,
  getDataDetailApplicant,
  resetState,
  activeApplicantId,
}: TScreeninTab) {
  const [activeTab, setActiveTab] = React.useState(0);
  return (
    <Tabs value={activeTab}>
      <TabsHeader
        className="rounded-none border-b border-blue-gray-50 bg-transparent p-0 "
        indicatorProps={{
          className: 'bg-transparent border-b-2 border-blue-800 shadow-none rounded-none',
        }}
      >
        <Tab value={0} onClick={() => setActiveTab(0)}>
          <Typography variant="small" className={clsx(activeTab === 0 ? 'text-blue-700 font-semibold' : 'text-inherit')}>
            Screening
          </Typography>
        </Tab>
        <Tab value={1} onClick={() => setActiveTab(1)}>
          <Typography variant="small" className={clsx(activeTab === 1 ? 'text-blue-700 font-semibold' : 'text-inherit')}>
            QC
          </Typography>
        </Tab>
        <Tab value={2} onClick={() => setActiveTab(2)}>
          <Typography variant="small" className={clsx(activeTab === 2 ? 'text-blue-700 font-semibold' : 'text-inherit')}>
            Done
          </Typography>
        </Tab>
      </TabsHeader>

      <TabsBody>
        <TabPanel value={0} className="p-0">
          <TableScreening
            dataTable={dataTable}
            getScreeningForm={getScreeningForm}
            getScreeningDetailDocument={getScreeningDetailDocument}
            getDataDetailApplicant={getDataDetailApplicant}
            resetState={resetState}
            activeApplicantId={activeApplicantId}
          />
          <PaginationTableScreening />
        </TabPanel>
        <TabPanel value={1}>
          <Typography variant="small">Ini QC</Typography>
        </TabPanel>
        <TabPanel value={2}>
          <Typography variant="small">Ini Done</Typography>
        </TabPanel>
      </TabsBody>
    </Tabs>
  );
}

function TableScreening({ dataTable, getScreeningForm, getScreeningDetailDocument, getDataDetailApplicant, resetState, activeApplicantId }: any) {
  const TABLE_HEAD = ['Name & Profile'];

  const onClick = (applicant_id: string) => {
    resetState();
    getScreeningForm(applicant_id);
    getScreeningDetailDocument(applicant_id);
    getDataDetailApplicant(applicant_id);
  };

  return (
    <div className="mt-2">
      <table className="w-full text-left border-separate border-spacing-0 rounded-lg border overflow-hidden">
        <thead>
          <tr>
            {TABLE_HEAD.map((head) => (
              <th key={head} className="border-b border-blue-gray-100 bg-blue-gray-50 p-4">
                <Typography variant="small" color="blue-gray" className="font-normal leading-none opacity-70">
                  {head}
                </Typography>
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {dataTable.map((item, index) => {
            return (
              <tr key={index} onClick={() => onClick(item.applicant_id)} className="hover:bg-gray-50 cursor-pointer">
                {/* <td width={'10px'} className="text-center">
                  {index + 1}
                </td> */}
                <td
                  className={clsx('p-2 border-b-gray-100', activeApplicantId === item.applicant_id ? 'border-r-blue-700 border-r-2 border-b-2' : '')}
                >
                  <p color="blue-gray" className="font-normal text-base truncate max-w-full">
                    {item.fullname}
                  </p>
                  <Typography color="blue-gray" className="font-normal text-xs text-gray-600">
                    {item.email}
                  </Typography>
                  <Typography color="blue-gray" className="font-normal mt-2 text-sm">
                    {item.identity_no}
                  </Typography>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

function PaginationTableScreening() {
  return (
    <div className="mt-2 font-normal -mr-6">
      <div className="flex items-center space-x-2">
        <label htmlFor="select_per_page">
          <p className="text-sm text-slate-700">Rows per page</p>
        </label>
        <select name="" id="select_per_page" className="rounded-lg border p-1.5 ml-2 text-sm">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
        </select>
        <p className="text-xs">Showing 1-10 of 10.</p>
      </div>
    </div>
  );
}

