function ChecronDown() {
  return (
    <svg className="fill-blue-gray-900" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M12 15.5L6 9.5L7.4 8.1L12 12.7L16.6 8.1L18 9.5L12 15.5Z" />
    </svg>
  );
}

type TValidationCard = {
  validationData: any;
  children?: React.ReactNode;
  fieldsValueDocument?: any;
};

function ValidationCard({ validationData, children, fieldsValueDocument }: TValidationCard) {
  const [isOpenCollapse, setIsOpenCollapse] = React.useState(false);

  // console.log(fieldsValueDocument);
  const childrenArray = React.Children.toArray(children);
  const isLastChild = childrenArray.length === 1;

  return (
    <div className={clsx('border rounded-lg')}>
      <div className="cursor-pointer px-2 py-3  " onClick={() => setIsOpenCollapse(!isOpenCollapse)}>
        <div className="flex justify-between items-center">
          <div className="space-x-3 flex items-center">
            <div>
              <DocumentIcon />
            </div>
            <div>
              <p className="uppercase tracking-widest flex-1">validasi {validationData.doc_name}</p>
            </div>
          </div>
          <ChecronDown />
        </div>
      </div>

      <Collapse className="h-full" open={isOpenCollapse}>
        {children}
      </Collapse>
      {isOpenCollapse && !isLastChild && fieldsValueDocument && (
        <div className="p-2 rounded-b-lg space-y-2 border-t">
          <h4 className="font-bold after:content-['*'] after:text-red-500 after:ml-2">
            <span className="uppercase">{validationData.doc_name}</span> Notes
          </h4>
          <div className="px-4 py-3 rounded-lg bg-orange-50 border-orange-500 text-gray-600 text-xs border">
            <React.Fragment>
              {JSON.parse(fieldsValueDocument[`ocr_${validationData.doc_type}_notes_summary`])?.map((item, index) => {
                return <p key={index}>{item}</p>;
              })}
            </React.Fragment>
          </div>
        </div>
      )}
    </div>
  );
}

function ValidationComponentValue({ validationValue, fieldsValueDocument, validationData, handleChangeValueScreeningDocument }) {
  return (
    <React.Fragment>
      <div key={validationValue.id} className="border-b last:border-b-0 first:border-t">
        {fieldsValueDocument && (
          <div
            className={clsx(
              'rounded-md m-2 text-sm space-y-1.5 p-2 ',
              fieldsValueDocument[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos' ? 'bg-[#E8FFE2]' : 'bg-[#FFE2E2]'
            )}
          >
            <h4 className="font-bold">
              {validationValue.field_name} <span className="text-red-500 text-xs">*</span>
            </h4>
            <p className="text-gray-500 font-light">{`(Pastikan ${validationValue.field_name} sesuai)`}</p>
            <hr className="border-[0.5px] border-gray-500" />
            {fieldsValueDocument['type_engine'] === 'ocr' && (
              <div className="text-gray-500">
                <h5>
                  <b>OCR Value:</b>{' '}
                  <span className="font-light">{fieldsValueDocument[`ocr_${validationData.doc_type}_${validationValue.field_code}_value`]}</span>
                </h5>
              </div>
            )}
            {/* {fieldsValueDocument[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`] && ( */}
            <div className="text-gray-500">
              <h5>
                <b>Alasan:</b>{' '}
                <span className="font-light"> {fieldsValueDocument[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`]}</span>
              </h5>
            </div>
            {/* // )} */}

            <div className="space-x-1.5 w-full flex items-center">
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}
                value="lolos"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={handleChangeValueScreeningDocument}
                checked={fieldsValueDocument[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}>
                <span>Sesuai</span>
              </label>
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}
                value="gugur"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={handleChangeValueScreeningDocument}
                checked={fieldsValueDocument[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'gugur'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}>
                <span>Tidak Sesuai</span>
              </label>
            </div>
          </div>
        )}
      </div>
    </React.Fragment>
  );
}

function LastValidationComponentValue({ handleChangeValueScreeningDocument, fieldsValueDocument }) {
  const [listOptions, setListOptions] = React.useState([]);

  const getListOptions = async () => {
    try {
      const response = await api.post('/screening/notes-option', {
        type: 'document', //"{document/proctoring}"
      });
      const result = response.data.data;
      const normalizeOptions = result.map((item, index) => ({
        value: item.id,
        label: item.notes_name,
      }));
      setListOptions(normalizeOptions);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    if (fieldsValueDocument) {
      getListOptions();
    }
  }, [fieldsValueDocument]);

  if (!fieldsValueDocument) return;

  return (
    <div className="border-b last:border-b-0 pb-4 first:border-t">
      <div
        className={clsx(
          'rounded-md m-2 text-sm space-y-1.5 p-2 ',
          fieldsValueDocument[`final_status`] === 'qualified' ? 'bg-[#E8FFE2]' : 'bg-[#FFE2E2]'
        )}
      >
        <h4 className="font-bold">
          Validasi Akhir <span className="text-red-500 text-xs">*</span>
        </h4>
        <p className="text-gray-500 font-light">{`(Validasi dari keseluruhan data)`}</p>
        <hr className="border-[0.5px] border-gray-500" />

        <div className="space-x-1.5 w-full flex items-center">
          <input
            type="radio"
            id={`final_status_qualified`}
            value="qualified"
            name={`final_status`}
            onChange={handleChangeValueScreeningDocument}
            checked={fieldsValueDocument[`final_status`] === 'qualified'}
          />
          <label htmlFor={`final_status_qualified`}>
            <span>Lolos</span>
          </label>
          <input
            type="radio"
            id={`final_status_disqualified`}
            value="disqualified"
            name={`final_status`}
            onChange={handleChangeValueScreeningDocument}
            checked={fieldsValueDocument[`final_status`] === 'disqualified'}
          />
          <label htmlFor={`final_status_disqualified`}>
            <span>Gugur</span>
          </label>
        </div>
        <div className="space-y-2">
          <Select isMulti closeMenuOnSelect={false} className="w-full bg-white rounded text-sm " options={listOptions} />
          <div>
            <label htmlFor="text-area-final-notes">Notes</label>
            <textarea id="text-area-final-notes" className="rounded border p-2 inline-block w-full mt-2 min-h-44" />
          </div>
        </div>
      </div>
    </div>
  );
}

type TPreviewFile = {
  filename: string;
  user_applicant_id: string;
};

function PreviewFile({ filename, user_applicant_id }: TPreviewFile) {
  const [fileSrc, setFileSrc] = React.useState(null);

  const getPreviewFile = async () => {
    try {
      const response = await api.post('/preview-file', {
        user_applicant_id,
        filename,
      });
      const result = response.data;
      setFileSrc(result);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    if (filename && user_applicant_id) {
      getPreviewFile();
    }
  }, [filename, user_applicant_id]);

  return <div>{fileSrc ? <embed src={fileSrc} type="application/pdf" width="100%" height="400px" /> : <div>Data tidak ditemukan</div>}</div>;
}
